// in redux we have to create a store and combine it with react application
// so in this store we have reducer(reducer basically function)
import {createSlice,configureStore} from '@reduxjs/toolkit'

// creating a slice
const authSlice=createSlice({
    name:"auth",
    initialState:{
        isLogin:false
    },
    reducers:{
        login(state){
            state.isLogin=true
        },
        logout(state){
            state.isLogin=false
        }
    }
})

export const authActions=authSlice.actions

export const store=configureStore({

    reducer:authSlice.reducer,
})