import React, { useState } from 'react'
import { Box,Typography, TextField, Button } from '@mui/material'
import { Form, useNavigate } from 'react-router-dom'
import axios from 'axios'



const Register = () => {
  const navigate=useNavigate()
  const [inputs,setinputs]=useState({
    name:'',
    email:'',
    password:''
  })


  // handle input change
  const handleChange=(e)=>{
    setinputs((prevState)=>({
      ...prevState,   //previous state ko rakhna hai as it is
      [e.target.name]:e.target.value,
    }));
  }; 

  // handle form submit
  const handlesubmit=async(e)=>{
    // whenever the form is submit the page refreshes therfore to handle this
    e.preventDefault();
    try {
      // network request
      const {data}=await axios.post('/api/v1/user/register',{username:inputs.name,
        email:inputs.email,
        password:inputs.password})
      if(data.success){
        alert('User Register Successfully');
        navigate("/login");
      }
    } catch (error) {
      console.log(error)
    }
  }

  
  return (
    <div>
      <form onSubmit={handlesubmit}>
        <Box maxWidth={450} display={"flex"} flexDirection={"column"} alignItems={"center"} justifyContent={"center"} margin={"auto"} marginTop={5} boxShadow={"10px 10px 20px #ccc"}
         padding={3} borderRadius={5}>
          <Typography variant='h4' padding={3} textAlign={'center'} sx={{textTransform:"uppercase"}}>Register</Typography>
          <TextField placeholder='name' name='name' margin='normal' type={"text"} required value={inputs.name} onChange={handleChange}></TextField>
          <TextField placeholder='email' name='email' margin='normal' type={"email"} required value={inputs.email} onChange={handleChange}></TextField>
          <TextField placeholder='password' name='password' margin='normal' type={"password"}required value={inputs.password} onChange={handleChange}></TextField>
          <Button type='submit' variant='contained' color='primary' sx={{borderRadius:3, marginTop:3}}>Submit</Button>
          <Button onClick={()=>navigate("/login")}type='submit' variant='contained' color='primary' sx={{borderRadius:3, marginTop:3}}>Already Register ? Please Login</Button>
        </Box>
      </form>  
    </div>
  )
}

export default Register