import React, { useState } from 'react'
import {Box,AppBar,Toolbar,Button, Typography, Tabs, Tab} from '@mui/material'
import { Link } from 'react-router-dom';
import { useSelector,useDispatch } from 'react-redux'
import { authActions } from '../redux/store';
import { useNavigate } from 'react-router-dom';
const Header = () => {
    // global state
    const isLogin=useSelector(state=>state.isLogin);
    const navigate=useNavigate();
    const dispatch=useDispatch();
    // state
    const [value,setValue]=useState();

    // handleing logout
    const logouthandle=(e)=>{
        try {
            dispatch(authActions.logout())
            alert('logout successfully')
            navigate('/login');
            localStorage.clear();
        } catch (error) {
            console.log(error);
        }
        
    }
  return (
    <div>
        <AppBar position='sticky'>
            <Toolbar>
                <Typography variant='h4'>
                    Blog App
                </Typography>
                {isLogin && <><Box display={'flex'} marginLeft="auto" marginRight={'auto'}>
                    {/* creating tabs for switching all to blogs to single blog */}
                    <Tabs textColor='inherit' value={value} onChange={(e,val)=>setValue(val)}>
                        <Tab label='Blogs' LinkComponent={Link} to="/blogs"/>
                        <Tab label="My Blogs" LinkComponent={Link} to="/my-blogs"/>
                        <Tab label="Create Blog" LinkComponent={Link} to="/create-blog" />
                    </Tabs>
                </Box></>}
                <Box display={'flex'} marginLeft={'auto'}>
                    {!isLogin && <><Button sx={{margin:1,color:'white'}} LinkComponent={Link} to="/login">Login</Button>
                    <Button sx={{margin:1,color:'white'}} LinkComponent={Link} to="/register">Register</Button></> }
                    {isLogin && <><Button sx={{margin:1,color:'white'}} onClick={logouthandle}>Logout</Button></>}
                </Box>
            </Toolbar>
        </AppBar>
    </div>
  )
}

export default Header;