import React, { useState } from 'react'
import { Box,Typography, TextField, Button } from '@mui/material'
import { Form, useNavigate } from 'react-router-dom'
import axios from 'axios'
import { useDispatch } from 'react-redux';
import { authActions } from '../redux/store';

const Login = () => {
  const navigate=useNavigate()
  // THE LOGIN MENU WHICH HAVE TO BE CHANGE AFTER SUCEESFULL LOGIN 
  // FOR THIS
  const dispatch=useDispatch();

  
  // state
  const [inputs,setinputs]=useState({
    email:'',
    password:''
  })


  // handle input change
  const handleChange=(e)=>{
    setinputs((prevState)=>({
      ...prevState,   //previous state ko rakhna hai as it is
      [e.target.name]:e.target.value,
    }));
  }; 

  // handle form submit
  const handlesubmit=async(e)=>{
    // whenever the form is submit the page refreshes therfore to handle this
    e.preventDefault();
    try {
      // network request
      const {data}=await axios.post('/api/v1/user/login',{
        email:inputs.email,
        password:inputs.password})
      if(data.success){
        // saving the userid because if we want to show only user blogs than it will helps
        localStorage.setItem('userId',data?.user._id);
        dispatch(authActions.login()) //changing the login state
        alert('User login Successfully');
        navigate("/");
      }
    } catch (error) {
      console.log(error)
    }
  }

  
  return (
    <div>
      <form onSubmit={handlesubmit}>
        <Box maxWidth={450} display={"flex"} flexDirection={"column"} alignItems={"center"} justifyContent={"center"} margin={"auto"} marginTop={5} boxShadow={"10px 10px 20px #ccc"}
         padding={3} borderRadius={5}>
          <Typography variant='h4' padding={3} textAlign={'center'} sx={{textTransform:"uppercase"}}>Login</Typography>
      
          <TextField placeholder='email' name='email' margin='normal' type={"email"} required value={inputs.email} onChange={handleChange}></TextField>
          <TextField placeholder='password' name='password' margin='normal' type={"password"}required value={inputs.password} onChange={handleChange}></TextField>
          <Button type='submit' variant='contained' color='primary' sx={{borderRadius:3, marginTop:3}}>Login</Button>
          <Button onClick={()=>navigate("/register")}type='submit' variant='contained' color='primary' sx={{borderRadius:3, marginTop:3}}>Not a user ? Please Register</Button>
        </Box>
      </form>  
    </div>
  )
}

export default Login