import React from 'react'
import { useState,useEffect } from 'react'
import axios from 'axios'
import BlogCard from '../components/BlogCard'
const Blogs = () => {
  const [blogs,setblogs]=useState([])
  // get blogs
  const getAllBlogs=async()=>{
    try {
      const {data}=await axios.get('/api/v1/blog/all-blog')
      if(data?.success){   //agar data milta hai toh uski query add hongi 
        setblogs(data?.blogs)
      }
    } catch (error) {
      console.log(error);
    }
  }
  useEffect(()=>{
    getAllBlogs();
  },[]);
  return (
    <div>
      {/* for every blog first blog will we find and than use for this we use map */}
      {blogs && blogs.map((blog)=><BlogCard
        // for deleting and updation we required id
        id={blog._id}
        
        //When you're not entirely sure if a property exists, 
        //use optional chaining (?.) to safely access nested properties.
        // This is particularly useful in JavaScript when dealing with 
        //deeply nested structures:
        isUser={localStorage.getItem("userId") === blog.user?._id}





        title={blog.title}
        description={blog.description}
        image={blog.image}
        username={blog.username} 
        time={blog.createdAt}
      />)}
        
    </div>
  )
}

export default Blogs