import React, { useEffect } from 'react'
import { useState } from 'react'
import axios from 'axios'
import { useNavigate, useParams } from 'react-router-dom';
import {Box, Button, InputLabel, TextField, Typography} from '@mui/material'
import { Navigate } from 'react-router-dom';
const BlogDetails = () => {
    const [blog,setBlog]=useState([])
    const userId = useParams().id; // Assuming the id parameter represents the user's ID
    const navigate=useNavigate();
    const [inputs,setInputs]=useState({
        title:'',
        description:'',
        image:'',
    })

    // input change handle
    const handleChange=(e)=>{
        setInputs(prevState=>({
            ...prevState,
            [e.target.name]:e.target.value
        }))
    }



    // get blog details
    const getBlogDetails=async ()=>{
        try {
            // console.log({id})
            const {data}=await axios.get(`/api/v1/blog/get-blog/${userId}`)
            if(data?.success){
                setBlog(data?.blog)
                // ye data show hoga edit post mai
                setInputs({
                    title:data?.blog.title,
                    description:data?.blog.description,
                    image:data?.blog.image
                })
            }

        } catch (error) {
            console.log(error);
        }
    }
    useEffect(() => {
        getBlogDetails();
    }, [userId]);
    
    console.log(blog);


    // handle form submission
    const handleSubmit=async(e)=>{
        e.preventDefault()
        // console.log(inputs);
        try {
            // axios ki help se network request bheje ge
            const {data}=await axios.put(`/api/v1/blog/update-blog/${userId}`,{
                title:inputs.title,
                description:inputs.description,
                image:inputs.image,
                user:userId
            })
            console.log(data); 
            if(data.success){
                alert('Blog Updated')
                navigate('/my-blogs');
            }
        } catch (error) {
            console.log(error)
        }
    }
  return (
    <div>
        <form onSubmit={handleSubmit}>
           <Box width={"50%"}border={3} borderRadius={10} padding={3} margin="auto" boxShadow={"10px 10px 20px #ccc"} display={"flex"} flexDirection={"column"} marginTop={"30px"}>
         <Typography variant="h2" textAlign={"center"} fontWeight={"bold"} padding={3} color={"gray"}>Edit A Blog</Typography>
        <InputLabel sx={{mb:1,mt:2,fontSize:"24px",fontWeight:"bold"}}>Title</InputLabel>
        <TextField required name="title" value={inputs.title} onChange={handleChange} margin='normal' variant='outlined'/>

        {/* description */}
        <InputLabel sx={{mb:1,mt:2,fontSize:"24px",fontWeight:"bold"}}>Description</InputLabel>
        <TextField required name="description" value={inputs.description} onChange={handleChange} margin='normal' variant='outlined'/>

        {/* image */}
        <InputLabel sx={{mb:1,mt:2,fontSize:"24px",fontWeight:"bold"}}>Image URL</InputLabel>
        <TextField required name="image" value={inputs.image} onChange={handleChange} margin='normal' variant='outlined'/>

        <Button type="submit" color='primary' variant='contained'>Submit</Button>
        </Box>
     </form>
    </div>
  )
}

export default BlogDetails